import { GulpTask } from '@microsoft/gulp-core-build';
import * as Gulp from 'gulp';
import { exec } from 'child_process';
import * as path from 'path';
import * as http from 'http';
import { Server, ServerRequest, ServerResponse } from 'http';
import * as serveStatic from 'serve-static';
import * as glob from 'glob';
import * as finalhandler from 'finalhandler';

/**
 * CasperJS is a browser navigation scripting & testing utility written in Javascript for PhantomJS or SlimerJS.
 * This CasperJS task searches for files with the extension casperjstest.js and spawns child processes to execute
 * CasperJS tests. Test results will be reported to the build system and all errors will cause a build break.
 * PhantomJS has a known bug where it doesn't output errors to stderr, so some of the detailed error information
 * will be contained in the test result output.
 */
export class CasperJSTask extends GulpTask<void> {
  constructor() {
    super('casperjs-tests');
  }

  public executeTask(gulp: typeof Gulp, completeCallback: (result?: string) => void): NodeJS.ReadWriteStream {
    const osdpBuildTasksRoot: string = path.resolve(__dirname, '..', '..');
    process.env.CASPER_PATH = path.join(osdpBuildTasksRoot, '\\node_modules\\casperjs');
    process.env.CASPER_BIN = path.join(process.env.CASPER_PATH, '\\bin\\');
    process.env.PHANTOMJS = path.join(
      osdpBuildTasksRoot,
      '\\node_modules\\phantomjs-prebuilt\\lib\\phantom\\bin\\phantomjs.exe');

    const casperTestPromiseArray: Promise<string>[] = [];
    this._getFiles(path.join(process.cwd(), '\\lib')).then((fileNames: string[]) => {
      if (fileNames.length < 1) {
        this.log('No casperJS tests found');
        completeCallback();
      } else {
        /* tslint:disable:no-any */
        const serve: any = serveStatic(process.cwd());
        const server: Server = http.createServer((req: ServerRequest, res: ServerResponse) => {
          const done: any = finalhandler(req, res);
          /* tslint:enable:no-any */
          serve(req, res, done);
        });

        let port: number = this. _getRandomPortNumber();
        server.on('error', (e: NodeJS.ErrnoException) => {
          if (e.code === 'EADDRINUSE') {
            this.logVerbose('Address in use, retrying...');
            port = this. _getRandomPortNumber();
            setTimeout(() => {
                server.close();
                server.listen(port);
              }, 1000);
            }
          });

          server.listen(port);

        for (let i: number = 0; i < fileNames.length; i++) {
          const command: string =
          [process.env.PHANTOMJS,
          process.env.CASPER_BIN + 'bootstrap.js',
          '--casper-path=' + process.env.CASPER_PATH,
          '--cli test',
          fileNames[i],
          '--url='].join(' ');

          server.on('listening', () => {
            this.logVerbose('Listening to port ' + port);
            casperTestPromiseArray.push(this._startCasperTestOnChildProcess(command + 'http://localhost:' + port));
            if (casperTestPromiseArray.length === fileNames.length) {
              Promise.all(casperTestPromiseArray).then((errors: string[]) => {
                const errorMessage: string = this._createCasperJSErrorMessage(errors);
                this.logVerbose('tests run on ' + fileNames.length + ' files');
                errorMessage === '' ? completeCallback() : completeCallback(errorMessage);
                server.close();
              });
            }
          });
        }
      }
    });
    return;
  }

  private _createCasperJSErrorMessage(errors: string[]): string {
    return errors.filter((value: string) => {
      return value !== '';
    }).join();
  }

  private _getFiles(dir: string): Promise<string[]> {
    return new Promise<string[]>(
      (complete?: (result: string[]) => void) => {
        glob('**/*.caspertest.js', {cwd: dir}, (er, files) => {
          complete((files || []).map((name: string) => {
            return path.resolve(dir, name);
          }));
        });
      });
  }

  private _startCasperTestOnChildProcess(cmd: string): Promise<string> {
    return new Promise<string>(
      (complete?: (result: string) => void) => {
        exec(cmd, (error: string, stdout: string, stderr: string) => {
          this.log('CasperJS Output: ' + stdout);
          complete([stderr, error].filter((value: string) => {
            return value && value !== '';
          }).join('\n'));
        });
      });
  }

  private _getRandomPortNumber(): number {
    const MAX_PORT: number = 65535;
    const MIN_PORT: number = 0;
    return Math.floor(Math.random() * (MAX_PORT - MIN_PORT + 1)) + MIN_PORT;
  }
}
